import { Image } from "antd"
import m from './Movie.module.css'

type propsType = {
    movie: {
        Title: string
        Poster: string
        Year: string
    } 
}

const Movie: React.FC<propsType> = ({movie}) => {
    const voidImg = 'data:image/png;base64,'
    return (
        <>
            <h2 className={m.title} >{movie.Title}</h2>
            {movie.Poster === 'N/A'
                ? <Image
                    className={m.img}
                    width={280}
                    height={370}
                    src="error"
                    fallback={voidImg}
                />
                : <Image
                    className={m.img}
                    height={370}
                    width={280}
                    src={movie.Poster}
                    fallback={voidImg}
                />
            }
            <div className={m.year}>{movie.Year}</div>
        </>
    )
}

export default Movie

